import React, { useState } from 'react';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import axios from 'axios';

import SearchForm from './SearchForm';
import StudentTable from './StudentTable';
import StudentModel from './StudentModel';
import EditFieldsForm from './EditFieldsForm';
import Loading from '../../Util/Loading';

const initialFields = {
  enrollment: true,
  name: true,
  semester: true,
  email: true,
  profile: true,
};

const SearchStudent = () => {
  const [students, setStudents] = useState(null);
  const [stud, setStud] = useState(null);
  const [loading, setLoading] = useState(false);
  const [fields, setFields] = useState(initialFields);

  const EditFields = () => {
    return (
      <div className="my-3">
        <div className="mb-3 text-right">
          <button
            className="btn btn-outline-info btn-sm"
            data-toggle="collapse"
            data-target="#editFields"
          >
            <i class="fad fa-edit"></i>
          </button>
          <ReactHTMLTableToExcel
            id="test-table-xls-button"
            className="btn btn-success ml-3"
            table="table-to-xls"
            filename={new Date().toLocaleDateString() + '_Students_Data_LDCE'}
            sheet="students"
            buttonText="Download Excel File"
          />
        </div>
        <div className="collapse" id="editFields">
          <EditFieldsForm
            onSubmit={(values) => setFields(values)}
            initialValues={fields}
            destroyOnUnmount={false}
          />
        </div>
      </div>
    );
  };

  const handleSubmit = async (values) => {
    setLoading(true);
    await axios
      .get('/api/admin/searchStudent', { params: values })
      .then((res) => {
        setStudents(res.data);
      })
      .catch((err) => console.log(err));
    setLoading(false);
  };
  return (
    <div className="container mt-5">
      <SearchForm onSubmit={handleSubmit} />
      {loading ? (
        <Loading />
      ) : (
        students &&
        (students.length === 0 ? (
          <h1 className="text-center">No Student Found!</h1>
        ) : (
          <>
            <EditFields />

            <StudentTable data={students} setStud={setStud} fields={fields} />
            {fields.profile && <StudentModel credential={stud} />}
          </>
        ))
      )}
    </div>
  );
};

export default SearchStudent;
